/*----------------------------------------------*/
/*	Generic style								*/
/*----------------------------------------------*/
body, input, textarea, button, option {
	font-family: Helvetica, Arial, sans-serif;
	font-size: 1rem;
}

.setting-bloc-help {
	line-height: 1.3;
	font-size: 1rem;
}

.setting-bloc-help h1 {
	font-family: Helvetica, Arial, sans-serif;
	letter-spacing: 1px;
	padding-top: 0.4em;
	margin-bottom: 0.5em;
}

.setting-bloc-help h2 {
	font-family: Helvetica, Arial, sans-serif;
}

.setting-bloc-help h3 {
	font-family: Helvetica, Arial, sans-serif;
	display: inline;
	vertical-align: middle;
	line-height: 1.2em;
	padding-right: 0.5em;
	margin: 2em 0 .5em;
	font-weight: bold;
	clear: both;
}

.setting-bloc-help h4 {
	font-family: Helvetica, Arial, sans-serif;
	padding-top: .625rem;
	margin: .625rem 0 0 0;
	font-weight: bold;
	clear: both;
}

.setting-bloc-help h5 {
	font-family: Helvetica, Arial, sans-serif;
	font-weight: bold;
	padding: 1.5em 0 0.5em 0;
	color: #F16e00;
	clear: both;
}

.uci-plus-orange-help {
	color: #F16E00;
	font-size: 130%;
	line-height: 0;
	font-weight: bold;
	padding-bottom: 0.125em;
	vertical-align: middle;
}

/*----------------------------------------------*/
/* Page How it works 							*/
/*----------------------------------------------*/


.setting-bloc-help a {
	color: #F16e00;
}

#o_content .setting-bloc-help .figure {
	width: 400px;
	margin: 1em auto;
	float: none;
}

#o_content .setting-bloc-help .figure img {
	padding: 0;
}

ul {
	list-style-type: disc;
	padding: .5em 2em 2em;
}

.setting-bloc-help ol {
	list-style: circle inside none;
}

.setting-bloc-help .browser_list ol {
	margin: 0 0 1em 0;
}

.setting-bloc-help .title_orange {
	color: #F16e00;
}

.setting-bloc-help h1,
.setting-bloc-help h2,
.setting-bloc-help h3,
.setting-bloc-help h4,
.setting-bloc-help h5,
.setting-bloc-help h6 {
	margin-bottom: 1.25rem;
	font-family: inherit;
	font-weight: 700;
	line-height: 1.1;
	color: inherit;
}

.setting-bloc-help h1 {
	font-size: 2.125rem;
}

.setting-bloc-help h2 {
	font-size: 1.875rem;
}

.setting-bloc-help h3 {
	font-size: 1.5rem;
}

.setting-bloc-help h4 {
	font-size: 1.25rem;
}

.setting-bloc-help h5 {
	font-size: 1.125rem;
}

.setting-bloc-help h6 {
	font-size: 1rem;
}

@media (max-width: 767px) {
	.setting-bloc-help h1 {
		font-size: 1.25rem;
	}

	.setting-bloc-help h2 {
		font-size: 1.125rem;
	}

	.setting-bloc-help h3 {
		font-size: 1rem;
	}

	.setting-bloc-help h4 {
		font-size: 0.875rem;
	}

	.setting-bloc-help h5 {
		font-size: 0.875rem;
	}

	.setting-bloc-help h6 {
		font-size: 0.875rem;
	}
}
